import React, { Component } from 'react'

class RandomKid extends Component {
	constructor(props) {
		super(props)
		this.state = {
			winner: "",
		}
	}

	pickWinner = (contestants) => {
		let newWinnerIndex = [Math.floor(Math.random() * contestants.length)];
		this.setState({winner: contestants[newWinnerIndex].name});
		this.props.handleRandom(newWinnerIndex);
	}

	render() {
		const { characterData } = this.props;

		if (characterData.characters.length > 1 && characterData.history.length === 0) {
			return (
				<div>
					<button onClick={() => this.pickWinner(characterData.characters)}>Pick a kid!</button>
				</div>
			)
		}
			else if (characterData.characters.length > 1) {
				return (
					<div>
						<button onClick={() => this.pickWinner(characterData.characters)}>Pick a kid!</button>
						<p>You picked {this.state.winner}!</p>
					</div>
				)
			}
				else {
					return (
						<div>
						</div>
					)
				}
	}
}

export default RandomKid