import React, { Component } from 'react';
import Table from './Table'
import Form from './Form'
import RandomKid from './RandomKid'

class App extends Component {
	constructor(props) {
		super(props)

		this.state = {
			characters: [],
			history: [],
		}
	}

	removeCharacter = index => {
		const { characters } = this.state

		this.setState({
			characters: characters.filter((character, i) => {
				return i !== index
			}),
		})
	}

	handleSubmit = character => {
		this.setState({ characters: [...this.state.characters, character] })
	}

	handleRandom = winnerIndex => {
		const winner = this.state.characters[winnerIndex];
		this.setState({ history: [...this.state.history, winner.name] });
		this.setState({ winner: winner.score++ });
	}

	render () {
		return (
			<div className="Container">
				<header>
					<h1>Random Kid Picker</h1>
				</header>
				<Form handleSubmit={this.handleSubmit} />
				<Table characterData={this.state.characters} removeCharacter={this.removeCharacter} />
				<RandomKid characterData={this.state} handleRandom={this.handleRandom} />
			</div>
		)
	}
}

export default App